.navbar-brand:hover {
  cursor: pointer;
}

.form-control {
  border: none;
  border-bottom: 1px solid #ccc;
}
.btn {
  border: 1px solid black;
  color: rgb(119, 119, 119);
}

.btn:hover {
  border: 1px solid black;
  background-color: #e6e6e6;
  background-image: linear-gradient(#f2f2f2, #e6e6e6);
  color: black;
}
.d-flex {
  border: 1px solid black;
  background-color: #ccc;
  border-radius: 50%;
}

.d-flex:hover {
  cursor: pointer;
}

:root {
  --bg-color: #fff;
  --text-color: #000;
}
[data-theme="dark"] {
  --bg-color: #3f3f3f;
  --text-color: #fff;
}
